import {useState} from 'react'
import './ContactForm.css';

function ContactForm(props) {
	// const [usermail,setUsermail] = useState("");
	const [title,setTitle] = useState("");
	const [contents,setContents] = useState("");
  return (
    <div className="ContactForm">
	  <div className="ContactFormContent">
		<div className="CloseSpan">
	      <strong className="close" onClick={()=>props.closeForm(false)}>X</strong>
		</div>
		<div className="TheForm">
			<input type="text" name="title" placeholder="Subject" value={title} onChange={(e)=>setTitle(e.target.value)} />
			<textarea type="text" name="content" placeholder="Message" value={contents} onChange={(e)=>setContents(e.target.value)} /><br />
			<div className="TheAncre" >
			<a href={"mailto:studioscroustillants@outlook.com?subject="+title+"&body="+contents.replace(/\n/g, '%0D%0A')} target="_blank"
          rel="noopener noreferrer" onClick={()=>props.closeForm(false)} >Send</a>
		  </div>
		</div>
	  </div>
    </div>
  );
}

			// <input type="mail" name="contact" placeholder="Your mail address" value={usermail} onChange={(e)=>setUsermail(e.target.value)} />
			// <input type="submit" value="Send" />
export default ContactForm;
